<template>

  <div  v-loading="isLoading"
        element-loading-text="">
    <div class="doc-desc">
      <h3>
        
        <el-button @click="editDescription" class="fr" size="small"></el-button>
      </h3>
      <transition name="desc"
                  enter-active-class="animated bounceInRight"
                  leave-active-class="animated bounceOutRight">
        <el-form v-if="isEditing" ref="form" :model="toUpdateDocument" label-width="80px">
          <el-form-item label="">
            <el-input :rows="5" type="textarea" v-model="toUpdateDocument.overview"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" :loading="submitLoading" @click="updateDocument"></el-button>
            <el-button @click="isEditing=false"></el-button>
          </el-form-item>
        </el-form>
      </transition>
      <pre v-if="!isEditing" >{{document.overview}}</pre>

    </div>


    <div class="imgs-show">
      <h3></h3>
      <ul>
        <li class="img-item" v-for="(img,index) in detailImgList">
          <a target="_blank" :href="img.path|addBaseUrl">
            <img  :src="img.path|addBaseUrl" :alt="img.alt">
          </a>
          <div class="img-info">
            <span>{{img.alt | imgNameFilter}}</span>

            <el-button class="fr" size="mini" icon="delete" type="danger" @click="deleteImg(index,img.id)"></el-button>
          </div>

        </li>
      </ul>
    </div>


    <div class="upload-box">
      <el-upload
        class="upload-component"
        drag
        :action="uploadPrototypeFileURL"
        :on-success="handleSuccess"
        :on-error="handleError"
        mutiple>
        <i class="el-icon-upload"></i>
        <div class="el-upload__text"><em></em></div>
        <div class="el-upload__tip" slot="tip">jpg/png500kb</div>
      </el-upload>
    </div>
</div>

</template>

<script>
  import api from "@/api"
  import config from "@/config"
  import Msg from "@/components/msg"

  export default {
    data(){
      return {
        isLoading:false,
        isEditing:false,
        detailImgList:[],
        document:{},
        submitLoading:false,
        toUpdateDocument:{},
        documentId:this.$route.params.documentId
      }
    },
    created(){
      this.isLoading = true;
//      console.log(this.documentId)
      api.getDocumentById(this.documentId)
        .then(({data:msg})=>{

          this.isLoading = false;
          this.document = msg.data.document;
          this.detailImgList = msg.data.detailImgList;
        })
    },
    computed:{
      uploadPrototypeFileURL(){
        return api.uploadFileURL(this.documentId,2);
      }
    },

    methods:{
      updateDocument(){
        api.updateDocument(this.documentId,this.toUpdateDocument)
          .then(({data:msg})=>{
            this.isEditing = false;
            this.submitLoading = false;
            this.document.overview = msg.data.overview;
            this.document.detail = msg.data.detail;
          })
        this.submitLoading = true;
      },
      editDescription(){
        this.isEditing = !this.isEditing;
        this.toUpdateDocument.overview = this.document.overview;
      },
      deleteImg(index,imgId){
        Msg.confirm("?")
          .then(()=>{
            api.deleteFileById(this.documentId,imgId)
              .then(({data:msg})=>{
                if(!msg.error) {
                  Msg.alertSuccess("")
                  this.detailImgList.splice(index, 1);
                }
              })
          })


      },
      handleSuccess(response, file, fileList){
        if(response.error){
          Msg.alertError("!");
        }
        else{
          this.detailImgList.push(response.data);
          Msg.alertSuccess("!");
        }
      },
      handleError(response, file, fileList){
        Msg.alertError("!");
      },
    }
  }
</script>

<style scoped lang="scss">
.slide-enter,.slide-leave-active{/*    */
  /*transform: translateX(100%);*/
  opacity: 0;
}
  .slide-enter-active,.slide-leave-active{
    transition: transform 2.2s ease-in;
  }

 </style>



